import { Type } from '../core.types';
import { envProvider } from '../env/env';
import { AbstractUniversalEnv } from './universal_env.abstract';

export class UniversalEnvProvider {
    private env? : AbstractUniversalEnv;

    public get ReqPrefix() {
        return this.env?.getReqPrefix() || envProvider.ReqPrefix;
    }

    public use(Env: Type<AbstractUniversalEnv>) {
        this.env = new Env();
    }

    public assign(env: AbstractUniversalEnv) {
        this.env = env;
    }
}

export const universalEnvProvider = new UniversalEnvProvider();
